import { Component, Host, Element, h, Prop } from '@stencil/core';
import { Event, EventEmitter } from '@stencil/core';
import { addFeedback } from '../../services/feedback-service';

@Component({
  tag: 'feedback-component',
  styleUrl: 'feedback-component.css',
  shadow: true,
})
export class FeedbackComponent {
  @Prop() componentKey: string;
  @Prop() shadow: boolean;
  cssClass: string;

  @Event() cancelClicked: EventEmitter<boolean>;
  @Event() feedbackCreated: EventEmitter<boolean>;
  @Element() el: HTMLElement;
  feedback: string;

  constructor() {
    this.cssClass = '';
    if (this.shadow === true) {
      this.cssClass = this.cssClass.concat('shadow ');
    }
  }

  componentDidLoad() {
    const input = this.el.shadowRoot.querySelector('input');
    input.focus();
  }

  render() {
    return (
      <Host>
        <form>
          <input type='text' onInput={(event) => this.handleChange(event)} />
          <div>
            <button
              class={this.cssClass + 'cancel '}
              onClick={(event) => this.onCancel(event)}
            >
              Cancel
            </button>
            <button
              class={this.cssClass + 'confirm '}
              type='submit'
              onClick={(event) => this.addFeedback(event)}
            >
              Feedback
            </button>
          </div>
        </form>
      </Host>
    );
  }
  handleChange(event): void {
    this.feedback = event.target.value;
  }
  async addFeedback(event): Promise<void> {
    event.preventDefault();
    if (this.feedback) {
      try {
        const message = await addFeedback({
          componentKey: this.componentKey,
          feedback: this.feedback,
        });
        if (message === true) {
          this.feedbackCreated.emit(message);
        } else {
          //TODO: Check if this error can be catch outside via events
          console.error(message);
        }
      } catch (error) {
        //TODO: Check if this error can be catch outside via events
        console.error(error);
      }
    }
  }

  onCancel(event) {
    event.preventDefault();
    this.cancelClicked.emit(true);
  }
}
